// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------

  
.header{
  background-color: var(--color-black);
  position: fixed;
  z-index: 99;
  width: 100%;
  top: 0;

  &__wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 9rem;
    padding: 0 4rem;
    @media(max-width:100em){
      height: 8rem;
    }
    @media(max-width:90em){
      height: 7rem;
    }
    @media(max-width:37.5em){
      height: 6.4rem;
      padding:  1.5rem;
    }
  }

  &__logo{
    @media(max-width:100em){
      height: 3rem;
    }
    @media(max-width:90em){
      height: 3rem;
    }
    @media(max-width:37.5em){
      max-width: 15.7rem;
      height: 3.6rem;
    }
  }

  &__middle{
      .region-title{
        display: inline-flex;
        align-items: center;
        text-transform: uppercase;
        cursor: pointer;
        @media(max-width:37.5em){
          display: none;
        }
        .icon{
          width: 9px;
          height: 18px;
          stroke: var(--color-orange);
          margin-inline-end: 12px;
          @media(max-width:100em){
            width: 7px;
            height: 15px;
          }
        }

        span{
          font-size: 2.4rem;
          font-family: var(--font-regular);
          color: var(--color-orange);
          letter-spacing: 3px;
          display: inline-block;
          line-height: 1;
          @media(max-width:100em){
            font-size: 2rem;
          }
          
        }
      }
  }

  &__end{
    display: inline-flex;
    align-items: center;
  }
}


.menu-icon{
  cursor: pointer;
  @media(max-width:100em){
    transform: scale(.9);
  }
  @media(max-width:100em){
    transform: scale(.82);
  }
  @media(max-width:37.5em){
    transform: scale(.8);
  }
}
